.App
{
  .nav-btn
  {
    position: fixed;
    z-index: 12;
    right: 8.3%;
    top: 8.3%;
    opacity: 0;
    cursor: pointer;
    display: flex;
    color: var(--font-color2);
    height: 2.5rem;
    width: 2.5rem;
    @media screen and (max-width: 1000px)
    {
      transition: all .3s ease-in-out;
      opacity: 1;
    }
    @media screen and (min-width: 1000px)
    {
      transition: all .3s ease-in-out;
      opacity: 0;
    }
  }

  .sidebar
  {
    width: 16%;
    height: 100vh;
    background-color: var(--sidebar);
    position: fixed;
    z-index: 10;
    box-shadow: var(--card-shadow);
    @media screen and (max-width: 1000px)
    {
      transition: all .3s ease-in-out;
      transform: translateX(-150%);
    }
    @media screen and (min-width: 1000px)
    {
      transition: all .3s ease-in-out;
      transform: translateX(0);
    }
  }
  .nav-toggle
  {
    @media screen and (max-width: 1000px)
    {
      transition: all .3s ease-in-out;
      transform: translateX(0);
    }
  }

  .main-content
  {
    zoom: 100%;
    width: 84%;
    margin-left: 16%;
    background-color: var(--bg);
    min-height: 100vh;
    background-image: url(../Images/dots.svg);
    display: grid;
    position: relative;
    .content
    {
      zoom: 100%;
      margin-top: 5rem;
      margin-bottom: 10rem;
      margin-left: 10rem;
      margin-right: 10rem;
      //width: 80%;
    }
    @media screen and (max-width: 1000px)
    {
      transition: all .3s ease-in-out;
      margin-left: 0;
      width: 100%;
    }
    @media screen and (min-width: 1000px)
    {
      transition: all .3s ease-in-out;
      transform: translateX(0);
      margin-left: 16%;
    }
  }
}

